  from {
    height: 0%;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }

  to {
    height: 100%;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}

@-webkit-keyframes anim-close {
