.clap {
  --btn-dimension: 4vw;
  --primary-color: rgb(253, 254, 255);
  --secondary-color: rgb(255, 255, 255);

  position: relative;
  outline: 1px solid transparent;
  border-radius: 50%;
  border: 2px solid var(--primary-color);
  width: var(--btn-dimension);
  height: var(--btn-dimension);
  background: none;
}

.clap:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  border-radius: 50%;
  width: calc(var(--btn-dimension) - 1px);
  height: calc(var(--btn-dimension) - 1px);
}

.clap:hover {
  cursor: pointer;
  border: 1px solid var(--secondary-color);
  transition: border-color 0.3s ease-in;
}

.clap:hover::after {
  -webkit-animation: shockwave 1s ease-in infinite;
          animation: shockwave 1s ease-in infinite;
}

svg {
  width: 2.5vw;
  fill: none;
  stroke: var(--secondary-color);
  stroke-width: 3px;
}

@-webkit-keyframes shockwave {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    box-shadow: 0 0 2px var(--secondary-color);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
    box-shadow: 0 0 50px darken(var(--secondary-color), 20%), inset 0 0 10px var(--secondary-color);
  }
}

@keyframes shockwave {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    box-shadow: 0 0 2px var(--secondary-color);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
    box-shadow: 0 0 50px darken(var(--secondary-color), 20%), inset 0 0 10px var(--secondary-color);
  }
}
